html {
  box-sizing: border-box;
  font-size: 16px;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: 'Muli', sans-serif;
}
.flexHelper {
  flex-direction: row;
}
.nextPosition {
  position: absolute;
  top: 50%;
  right: 0;
}
.backPosition {
  position: absolute;
  top: 50%;
  left: 0;
}
@media only screen and (max-device-width: 480px) {
  .flexHelper {
    flex-direction: column;
  }
  .nextPosition {
    position: fixed;
    top: 80%;
    right: 80px;
  }
  .backPosition {
    position: fixed;
    top: 80%;
    left: 80px;
  }
}
